.menu {
  background-color: $main-Accent-red;
  overflow-y: auto;

  &__content {
    box-sizing: border-box;
    height: 100vh;
    padding: $main-padding-desktop 0;
  }

  &__top {
    margin-bottom: 48px;
  }
  &__list {
    margin: 0 0 48px;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;

    transition: border-color 0.3s;

    &:hover {
      border-color: #fff;
    }
  }

  &__call {
    display: block;
    width: max-content;
    margin-bottom: 8px;

    font-weight: 700;
    font-size: 16px;
    line-height: 27px;
    text-transform: uppercase;
    text-decoration: none;
    color: #fff;

    &--section {
      margin-bottom: 16px;
    }
    &--action {
      margin-bottom: 8px;
      border-bottom: 1px solid #fff;
    }
  }
}
